<template lang="html">
    <!--tabletable  -->
    <div class="">
        <Row>
            <Col span="24">
                <bread></bread>
            </Col>
        </Row>
        <Tabs>
            <TabPane label="" name="name1">
                <Row>
                    <Col span="24">
                        <Table :columns="columns1" :data="data1"></Table>
                    </Col>
                </Row>
                <Row style="margin: 10px 0">
                    <Page :total="100" show-sizer style="float:right "></Page>
                </Row>
                <Row style="margin: 10px 0">
                    <Col span="2" offset="1">
                        <Button type="primary" @click="getdata">
                           
                       </Button>
                    </Col>
                </Row>
            </TabPane>
            <TabPane label="" name="name2"></TabPane>
            <TabPane label="" name="name3"></TabPane>
        </Tabs>

    </div>
</template>

<script>
import {post} from '../../http'
import bread from '../../components/breadcrumb'
export default {
    data () {
        return {
            columns1: [
                {
                    title: 'Name',
                    key: 'name'
                },
                {
                    title: 'Age',
                    key: 'age'
                },
                {
                    title: 'Address',
                    key: 'address'
                },
                {
                    title: 'Date',
                    key: 'date'
                }
            ],
            data1: [
                {
                    name: 'John Brown',
                    age: 18,
                    address: 'New York No. 1 Lake Park',
                    date: '2016-10-03'
                },
                {
                    name: 'Jim Green',
                    age: 24,
                    address: 'London No. 1 Lake Park',
                    date: '2016-10-01'
                },
                {
                    name: 'Joe Black',
                    age: 30,
                    address: 'Sydney No. 1 Lake Park',
                    date: '2016-10-02'
                },
                {
                    name: 'Jon Snow',
                    age: 26,
                    address: 'Ottawa No. 2 Lake Park',
                    date: '2016-10-04'
                }
            ],
            getJson: {
                'head': {
                    'module': 'object',
                    'function': 'get_obj_service',
                    'page_index': 1,
                    'page_size': 100
                },
                'body': [{
                    'name': '',
                    'type': 0,
                    'search': '',
                    'is_detail': false
                }]
            }
        }
    },
    components: {
        bread
    },
    methods: {
        getdata () {
            this.getTableData(this.getJson);
        },
        getTableData (Strdata) {
            return new Promise((resolve, reject) => {
                post('/data/',Strdata)
                    .then(response => {
                        resolve(response);
                        // console.log(response.data[0]);
                    }, err => {
                        reject(err);
                    })
            })
        }
    }
}
</script>

<style lang="css">
</style>



